import { roundRectColor,roundRectBorderColor, circleImg, drawText, drawCircle, drawTextWrap,drawLine } from '@/common/js/canvasUrils.js';
export default{
	data () {
		return {
			cardUrl: '',
			ctx: '',
			errMsg: []
		}
	},
	onShow() {
		
	},
	methods:{
		async fnCreate () {
			try{
			    let img = await this.getCard();
			    
			    uni.saveImageToPhotosAlbum({
			        filePath: img,
			        success: () => {
			            uni.showToast({
			                title: '图片已保存至相册',
			                icon: 'none'
			            });
			        },
			        fail: err=>{
			            uni.showToast({
			                title: '图片保存失败',
			                icon: 'none'
			            });
			        }
			    });
			    
			}catch(e){
			    uni.showToast({
			        title: '生成图片失败',
			        icon: 'none'
			    });
			}
		},
		async getCard() {
		    let _this = this;
		
		    _this.ctx = uni.createCanvasContext('userCardCanvas', this);
		    const C_W = 375, //canvas宽度 整个图片宽度
		        C_H = 600;
		    let _strlineW = 0; //文本宽度
		    let _bgImage = await _this.getImageInfo({ imgSrc: 'http://pic.wxhand.com/dev/student_image/9e3f6cea939ce916e18b449d9549e49f!Thumbwidth320' });
		    
			console.log(_bgImage, '_bgImage');
		    _this.canvasW = C_W;
		    _this.canvasH = C_H;
		    // 黑色背景
		    roundRectColor(_this.ctx, 0, 0, C_W, C_H, 0, 'rgba(0,0,0,1)');
		    // 头像
		    circleImg(_this.ctx, _bgImage.path, 130, 30, 50, 50, 'rgba(255,255,255,0.5)');
		    drawText(_this.ctx, 'name: Jack Yang', 130, 210 , 12, '#fff', 180);
		    console.log('开始绘制');
		    _this.errMsg.push('绘制开始 ');
		    return new Promise((resolve, reject) => {
		        _this.ctx.draw(true, ret => {
		            console.log(ret);
		            _this.errMsg.push('draw success: '+ JSON.stringify(ret));
		            _this.getNewImage()
		                .then(imgUrl => {
		                    this.cardUrl = imgUrl;
		                    resolve(imgUrl);
		                    console.log('绘制完毕', imgUrl);
		                    _this.errMsg.push('绘制完毕: '+ imgUrl);
		                })
		                .catch(error => {
		                    console.log('绘制失败', error);
		                    _this.errMsg.push('绘制失败: '+JSON.stringify(error));
		                    reject(error);
		                });
		        });
		    });
		},
		async getImageInfo({ imgSrc }) {
		    let _this = this;
		    console.log('getImgInfo', imgSrc);
		    _this.errMsg.push('getImgInfo: '+imgSrc);
		    return new Promise((resolve, errs) => {
		        uni.getImageInfo({
		            src: imgSrc,
		            success: function(image) {
		                console.log('get imageINfo success', image);
		                _this.errMsg.push('get imageINfo success: '+ JSON.stringify(image));
		                resolve(image);
		            },
		            fail(err) {
		                console.log('getImageInfo失败,开启下载图片');
		                uni.downloadFile({
		                    url: imgSrc, //仅为示例，并非真实的资源
		                    success: (res) => {
		                        if (res.statusCode === 200) {
		                            resolve({ width: 250, height: 250, path: res.tempFilePath});
		                        }else{
		                            console.log('download fail', err);
		                            _this.errMsg.push('download fail: '+JSON.stringify(err));
		                            errs(err);
		                        }
		                    },
		                    fail: (e) => {
		                        console.log('download fail', e);
		                        this.errMsg.push('download fail: '+JSON.stringify(e));
		                        errs(e);
		                    }
		                });
		            }
		        });
		    });
		},
		getNewImage() {
		    return new Promise((resolve, reject) => {
		        uni.canvasToTempFilePath(
		            {
		                canvasId: 'userCardCanvas',
		                success: res => {
		                    resolve(res.tempFilePath);
		                },
		                fail: error => {
		                    reject(error);
		                }
		            },
		            this
		        );
		    });
		},
	}
}
